import {copyMaze, types, setSingleNodeType, getNodeLocation} from '../../../screens/PathfindingPage/mazeHelpers';

import Defaults from '../../../defaults';

import BasePathfinding from './BasePathfinding';

class DFS extends BasePathfinding{
    constructor(maze, setMaze, setMazePrev, finishFinding, waitTimeout) {
        super(maze, setMaze, setMazePrev, finishFinding, waitTimeout);
    }
    
    createVisitedArray(maze) {
        let sets = [];
        for(let i = 0 ; i < maze.length; ++i){
            let row = [];
            for(let j = 0 ; j < maze[0].length; ++j){
                row.push(false);
            }
            sets.push(row);
        }
        return sets;
    }

    updatePath(path, pathCopy){
        path.length = 0;
        pathCopy.forEach(element => {
            path.push(element)
        });
    }

    async find() {

        let startLocation = getNodeLocation(this.maze, types.start);
        let visited = this.createVisitedArray(this.maze);
        let path = [];
        await Defaults.delay(10);
        if(await this.DFSInner(startLocation[0], startLocation[1], visited, path)) {
            await Defaults.delay(100);
            for(let i = 0; i < path.length; ++i) {
                if(this.stop){
                    return false;
                }
                let type = types.path;
                if(this.maze[path[i][0]][path[i][1]].type == types.checkingStart){
                    type = types.pathStart;
                }
                else if(this.maze[path[i][0]][path[i][1]].type == types.checkingTarget) {
                    type = types.pathTarget;
                }
                setSingleNodeType(this.maze, path[i][0], path[i][1], type, this.setMaze, this.setMazePrev); 
                await Defaults.delay(this.waitTimeout);
            }
        }
        
        this.finishFinding();

    };

    async DFSInner(currentI, currentJ, visited, path) {
        if(this.stop){
            return false;
        }
        if(visited[currentI][currentJ]){
            return false;
        }
        path.push([currentI, currentJ]);

        if(this.maze[currentI][currentJ].type != types.target && this.maze[currentI][currentJ].type != types.start){
            setSingleNodeType(this.maze, currentI, currentJ, types.checking, this.setMaze, this.setMazePrev); 
            await Defaults.delay(this.waitTimeout);
        }
        else if(this.maze[currentI][currentJ].type == types.start){
            setSingleNodeType(this.maze, currentI, currentJ, types.checkingStart, this.setMaze, this.setMazePrev); 
            await Defaults.delay(this.waitTimeout);
        }
        else if(this.maze[currentI][currentJ].type == types.target){
            setSingleNodeType(this.maze, currentI, currentJ, types.checkingTarget, this.setMaze, this.setMazePrev); 
            await Defaults.delay(this.waitTimeout);
        }

        let pathCopy = [...path];

        visited[currentI][currentJ] = true;
        if(this.maze[currentI][currentJ].type == types.checkingTarget){
            return true;
        }
        // up
        if(currentI - 1 >= 0){
            if(this.maze[currentI - 1][currentJ].type != types.block && visited[currentI - 1][currentJ] == false){
                if(await this.DFSInner(currentI - 1, currentJ, visited, path)) {
                    return true;
                }
            }
        }
        this.updatePath(path, pathCopy);
        // right
        if(currentJ + 1 < this.maze[0].length){
            if(this.maze[currentI][currentJ + 1].type != types.block && visited[currentI][currentJ + 1] == false){
                if(await this.DFSInner(currentI, currentJ + 1, visited, path)) {
                    return true;
                }
            }
        }
        this.updatePath(path, pathCopy);
        // down
        if(currentI + 1 < this.maze.length){
            if(this.maze[currentI + 1][currentJ].type != types.block && visited[currentI + 1][currentJ] == false){
                if(await this.DFSInner(currentI + 1, currentJ, visited, path)) {
                    return true;
                }
            }
        }
        this.updatePath(path, pathCopy);
        // left
        if(currentJ - 1 >= 0){
            if(this.maze[currentI][currentJ - 1].type != types.block && visited[currentI][currentJ - 1] == false){
                if(await this.DFSInner(currentI, currentJ - 1, visited, path)) {
                    return true;
                }
            }
        }

        return false;
    };
}
  
export default DFS;